<template>
  <div class="task-detail flex flex-col">
    <!-- 頁面header -->
    <div id="task-header" class="w-full bg-yellow-300 p-2 pl-5">
      <button @click="$router.back()" class="c-clickable">
        <font-awesome-icon icon="arrow-left" class="mr-1" />返回
      </button>
    </div>

    <!-- 頁面content -->
    <div id="task-content" class="flex-1">
      <form>
        <label for="task-title">標題</label>
        <input type="text" id="task-title" v-model="title" />

        <label for="task-description">內容</label>
        <input type="textarea" id="task-description" v-model="description" />

        <date-range-picker
          ref="picker"
          :locale-data="{ firstDay: 1, format: 'dd-mm-yyyy HH:mm:ss' }"
          :singleDatePicker="datePickerOptions.singleDatePicker"
          :timePicker="datePickerOptions.timePicker"
          :timePicker24Hour="datePickerOptions.timePicker24Hour"
          :showWeekNumbers="datePickerOptions.showWeekNumbers"
          :showDropdowns="datePickerOptions.showDropdowns"
          :autoApply="datePickerOptions.autoApply"
          v-model="datePickerOptions.dateRange"
          :linkedCalendars="datePickerOptions.linkedCalendars"
        >
          <template
            v-slot:input="picker"
            style="min-width: 350px;"
          >{{ picker.startDate | date }} - {{ picker.endDate | date }}</template>
        </date-range-picker>
      </form>
    </div>
  </div>
</template>

<script>
import DateRangePicker from "vue2-daterange-picker";
import dayjs from "dayjs";
import "vue2-daterange-picker/dist/vue2-daterange-picker.css";


const dummtDetail = {
  title: '完成Kanban頁面',
  description: 'write something right here...',
  startDate: 'native Date Thu Jun 24 2021 12:00:00 GMT+0800 (台北標準時間)',
  endDate: 'native Date Sat Jul 03 2021 12:00:00 GMT+0800 (台北標準時間)'
}
export default {
  name: "TaskDetail",
  data() {
    return {
      kanbanId: "",
      title: "",
      description: "",
      datePickerOptions: {
        singleDatePicker: false,
        timePicker: false,
        timePicker24Hour: true,
        showWeekNumbers: false,
        showDropdowns: true,
        autoApply: true,
        linkedCalendars: true,
        dateRange: {}
      }
    };
  },
  components: {
    DateRangePicker
  },
  filters: {
    date(value) {
      if (!value) return "選擇日期";
      return dayjs(value).format("YYYY/MM/DD");
    }
  },
  methods: {
    fetchTaskDetail() {
      //Todo 根據kanbanId和index，取回對應的taskDetail

      this. title = dummtDetail.title
      this.description = dummtDetail.description
      this.datePickerOptions.dateRange = {
        startDate: dummtDetail.startDate,
        endDate: dummtDetail.endDate
      }
      
    }
  },
  created() {
    this.fetchTaskDetail()
  }
};
</script>